import React,{Component} from 'react'
import './imagesalign.css'
import Pic1 from './folder1/pic1.jpg';
import Pic2 from './folder1/pic2.jpg';

class Christian extends Component {
    render(){
        return(
            <div class="container-fluid">
                <h1 class="text-muted heading-styling">Christian Grave Stone</h1>
                <div class="row">
                    <div class="col-md-4">
                        <img src = {Pic1} class="img-responsive img-fluid rounded img-adjust" alt="picture1"/>
                    </div>

                    <div class="col-md-4">
                        <img src = {Pic2} class="img-responsive img-fluid rounded img-adjust" alt="picture2"/>
                    </div>

                </div>

            </div>
        )
    }
}

export default Christian;

