import React,{Component} from 'react'
import './imagesalign.css'
import Pic1 from './folder3/f3pic1.jpg';
import Pic2 from './folder3/f3pic2.jpg';
import Pic3 from './folder3/f3pic3.jpg';
import Pic4 from './folder3/f3pic4.jpg';


class Draupathi extends Component {
    render(){
        return(
            <div class="container-fluid">
                <h1 class="text-muted h1 heading-styling">Draupathi amman</h1>
            <div class="row">
                <div class="col-md-4">
                    <img src = {Pic1} class="img-responsive img-fluid rounded img-adjust" alt="picture1"/>
                </div>

                <div class="col-md-4">
                    <img src = {Pic2} class="img-responsive img-fluid rounded img-adjust" alt="picture2"/>
                </div>

            </div>

            <div class="row">
                <div class="col-md-4">
                    <img src = {Pic3} class="img-responsive img-fluid rounded img-adjust" alt="picture1"/>
                </div>

                <div class="col-md-4">
                    <img src = {Pic4} class="img-responsive img-fluid rounded img-adjust" alt="picture2"/>
                </div>

            </div>


        </div>

        )
    }
}

export default Draupathi;
