import React,{Component} from 'react'
import './imagesalign.css'
import Pic1 from './folder5/f5pic1.jpg';
import Pic2 from './folder5/f5pic2.jpg';
import Pic3 from './folder5/f5pic3.jpg';
import Pic4 from './folder5/f5pic4.jpg';
import Pic5 from './folder5/f5pic5.jpg';

class NameBoard extends Component {
    render(){
        return(
            <div class="container-fluid">
                <h1 class="text-muted h1 heading-styling">Name Board</h1>
            <div class="row">
                <div class="col-md-4">
                    <img src = {Pic1} class="img-responsive img-fluid rounded img-adjust" alt="picture1"/>
                </div>

                <div class="col-md-4">
                    <img src = {Pic2} class="img-responsive img-fluid rounded img-adjust" alt="picture2"/>
                </div>

            </div>

            <div class="row">
                <div class="col-md-4">
                    <img src = {Pic3} class="img-responsive img-fluid rounded img-adjust" alt="picture1"/>
                </div>

                <div class="col-md-4">
                    <img src = {Pic4} class="img-responsive img-fluid rounded img-adjust" alt="picture2"/>
                </div>

            </div>

            <div class="row">
                <div class="col-md-4">
                    <img src = {Pic5} class="img-responsive img-fluid rounded img-adjust" alt="picture1"/>
                </div>

            </div>


        </div>
        )
    }
}

export default NameBoard;
