import React,{Component} from 'react'
import './imagesalign.css'
import Pic1 from './folder2/f2pic1.jpg';
import Pic2 from './folder2/f2pic2.jpg';

class Church extends Component {
    render(){
        return(

           <div class="container-fluid">
               <h1 class="text-muted h1 heading-styling">Church</h1>
                <div class="row">
                    <div class="col-md-4">
                        <img src = {Pic1} class="img-responsive img-fluid rounded img-adjust" alt="picture1"/>
                    </div>

                    <div class="col-md-4">
                        <img src = {Pic2} class="img-responsive img-fluid rounded img-adjust" alt="picture2"/>
                    </div>

                </div>

            </div>
        )
    }
}

export default Church;
